  ngOnInit(): void {
    this.retrieveMyLogReport();
  }
  getRequestParams(searchTitle: string, page: number, pageSize: number): any {
    let params: any = {};
    if (searchTitle) {
      params[`search`] = searchTitle;
    }
    if (page) {
      params[`page`] = page;
    }
    if (pageSize) {
      params[`page_size`] = pageSize;
    }
    return params;
  }

  retrieveMyLogReport(field?: any, type?: any): void {
    let param: any = {};
    if (field && type) {
      param[`field`] = field;
      param[`order_in`] = type;
    }
    else
      param = this.getRequestParams(this.title, this.page, this.pageSize);
    this._logreportservice.getCustomerLogReports(param).subscribe((res: any) => {
      this.myLogReports = res.data.results;
      // this.showError = this.myLogReports.length == 0 ? true : false;
      this.totalMyLogReport = res.data.count;
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      })

  }

  handlePageChangeMyLog(event: number): void {
    this.myLogPage = event;
    this.retrieveMyLogReport();
  }

  onKeyUpMyLog(x: any) {
    this.title = x.toLowerCase().replace('log', '');
    this.myLogPage = 1;
    this.retrieveMyLogReport();
  }
