  retrieveLogReport(field?: any, type?: any): void {
    let param: any = {};
    if (field && type) {
      param = `field=${field}&order_in=${type}`;
    }
    else
      param = `page=${this.page}&page_size=${this.pageSize}`;
    // const params = this.getRequestParams(this.title, this.page, this.pageSize);
    // this._logreportservice.getLogReports(params).subscribe((res: any) => {
    this.crud.getRequest(`admin/reseller/logs?${param}`).then((res: any) => {
      this.logReports = res.data.results;
      this.showError = this.logReports.length == 0 ? true : false;
      this.totalLogReport = res.data.count;
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      })

  }
  getRequestList() {
    this.crud.getRequest(`admin/reseller/logs?page=${1}&page_size=${this.pageSize}&from_date=${this.fromDate}&to_date=${this.toDate}`).then((res: any) => {
      this.logReports = res.data.results;
      this.showError = this.logReports.length == 0 ? true : false;
      this.totalLogReport = res.data.count;
    })
  }

  handlePageChange(event: number): void {
    this.page = event;
    this.retrieveLogReport();
  }

  onKeyUp(x: any) {
    this.title = x;
    this.page = 1;
    this.retrieveLogReport();
  }
  serchFileAgainstDateMyLog() {
    // (click)="exportToCsv(logReports)"
    if (this.fromDate && this.toDate) {
      this.crud.getRequest(`customer/logs/csv/download?from_date=${this.fromDateMyLog}&to_date=${this.toDateMyLog}`).then((res: any) => {
        if (res) {
          this.exportToCsvMyLog(res.data);
        }
        document.getElementById('dateModalMyLog')?.click();
      })
    }
  }
  exportToCsvMyLog(data: any) {
    data.forEach((value: any, index: any) => {
      let method = '';
      let event = '';
      method = (value?.method == 'POST') ? 'Created' : ((value?.method == 'PUT') ? 'Updated' : value?.method);
      event = (value?.event == 'POST') ? 'Created' : ((value?.event == 'PUT') ? 'Updated' : value?.event);

      data[index]['method'] = method;
      data[index]['event'] = event;
      data[index]['#'] = `LOG${value?.sys_id}`
      const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
      let dt = new Date(value?.created_at);
      let date = dt.getDate();
      let month = dt.toLocaleString('default', { month: 'short' });
      let year = dt.getFullYear();
      let time = dt.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })
      data[index]['created_at'] = month + " " + date + " " + year + " | " + time;
      data[index]['description'] = (value?.description) ? (value?.description) : '';
      data[index]['title'] = (value?.title) ? (value?.title) : '';
      data[index]['file_request'] = (value?.file_request) ? (value?.file_request) : '';
      data[index]['credit_update_type'] = (value?.credit_update_type) ? (value?.credit_update_type) : '';
      data[index]['credit_update_description'] = (value?.credit_update_description) ? (value?.credit_update_description) : '';
    });

    let today = new Date().toLocaleDateString("de-DE");
    let filename = "log-report-" + today.replaceAll(".", "-");
    this._logreportservice.downloadInvoiceFile(data, filename);
  }

