import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { LogReportService } from 'src/app/admin/services/log-report.service';
import { CrudService } from 'src/app/services/crud.service';
// import { LogReportService } from '../../services/log-report.service';
import { NotificationsService } from 'src/services/notifications.service';

@Component({
  selector: 'app-report-view',
  templateUrl: './report-view.component.html',
  styleUrls: ['./report-view.component.scss']
})
export class ReportViewComponent implements OnInit {

  report_id: any;
  reportDetail: any;
  type: any = this.route.queryParams

  constructor(private route: ActivatedRoute, public notification: NotificationsService,
    private _titleservice: Title, private crud: CrudService) {
    this._titleservice.setTitle('Log report details')
    this.report_id = this.route.snapshot.paramMap.get('id');
  }

  ngOnInit(): void {
    this.getLogReportById();
  }

  getLogReportById() {
    // this._logreportservice.getLogById(this.report_id).subscribe((res:any)=>{
    var url;
    if (this.type['_value'].type == 'customer')
      url = 'customer/logs/detail/';
    else
      url = 'admin/reseller/logs/'
    this.crud.getRequest(`${url}${this.report_id}`).then((res: any) => {
      this.reportDetail = res.data;
    },
      (err: any) => {
      })
  }

}
