import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ReportListComponent } from './report-list/report-list.component';
import { ReportLogComponent } from './report-log.component';
import { ReportViewComponent } from './report-view/report-view.component';
import { CustomerReportListComponent } from './customer-report-list/customer-report-list.component';

const routes: Routes = [
  {
    path: '', component: ReportLogComponent, children: [
      { path: '', component: ReportListComponent },
      { path: 'customer', component: CustomerReportListComponent },
      { path: 'report-detail/:id', component: ReportViewComponent }
    ]
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReportLogRoutingModule { }
