import { Component, inject } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';
import { LogReportService } from 'src/app/admin/services/log-report.service';
import { CrudService } from 'src/app/services/crud.service';
import { LogParseService } from 'src/app/services/log-parse.service';
import { NotificationsService } from 'src/services/notifications.service';

@Component({
  selector: 'app-customer-report-list',
  templateUrl: './customer-report-list.component.html',
  styleUrls: ['./customer-report-list.component.scss']
})
export class CustomerReportListComponent {

  logReports: any = [];
  page = 1;
  pageSize = 10;
  title = '';
  totalLogReport: any;
  showError: boolean = false;
  fromDate: any;
  toDate: any;
  sortField: any;
  sortOrder: any;

  logParseService = inject(LogParseService);
  
  constructor(private _titleservice: Title, private toastr: ToastrService,
    private _logreportservice: LogReportService, private crud: CrudService,
    public notification: NotificationsService) {
    this._titleservice.setTitle('Log Report')
  }

  ngOnInit(): void {
    this.retrieveLogReport();
  }

  getRequestParams(params:any,searchTitle: string, page: number, pageSize: number): any {
    if (searchTitle) {
      params[`search`] = searchTitle;
    }
    if (page) {
      params[`page`] = page;
    }
    if (pageSize) {
      params[`page_size`] = pageSize;
    }
    return params;
  }

  retrieveLogReport(field?: string, order?: string): void {
    let param: any = {};

    if(field) {
      this.sortField = field;
      this.sortOrder = order;
    }
    if (this.sortField) {
      param[`field`] = this.sortField;
      param[`order_in`] = this.sortOrder;
    }
    this.getRequestParams(param,this.title, this.page, this.pageSize);

    if(this.fromDate) {
      param.from_date = this.fromDate;
      param.to_date = this.toDate;
    }
    this._logreportservice.getCustomerLogReports(param).subscribe((res: any) => {
      this.logReports = res.data.results;
      this.logReports.map((log:any) => this.logParseService.parseDetails(log))
      this.showError = this.logReports.length == 0 ? true : false;
      this.totalLogReport = res.data.count;
      document.getElementById('dateModal')?.click();
    },
      (err: any) => {
        this.toastr.error(err.error.detail, '', { timeOut: 4000 });
      })

  }

  handlePageChange(event: number): void {
    this.page = event;
    this.retrieveLogReport();
  }

  onKeyUp(x: any) {
    this.title = x.toLowerCase().replace('log', '');
    this.page = 1;
    this.retrieveLogReport();
  }

  exportData: any = [];
  
  
  serchFileAgainstDate() {
    // (click)="exportToCsv(logReports)"
    if (this.fromDate && this.toDate) {
      this.crud.getRequest(`customer/logs/csv/download?from_date=${this.fromDate}&to_date=${this.toDate}`).then((res: any) => {
        if (res) {
          this.exportToCsv(res.data);
        }
        document.getElementById('dateModal')?.click();
      })
    }
  }

  // Filter log reports list based on date range 
  searchByRange(isReset = false) {
    if(isReset) {
      this.fromDate = '';
      this.toDate = '';
    }
    this.retrieveLogReport();
  }


  exportToCsv(data: any) {
    data.forEach((value: any, index: any) => {
      let method = '';
      let event = '';
      method = (value?.method == 'POST') ? 'Created' : ((value?.method == 'PUT') ? 'Updated' : value?.method);
      event = (value?.event == 'POST') ? 'Created' : ((value?.event == 'PUT') ? 'Updated' : value?.event);

      data[index]['method'] = method;
      data[index]['event'] = event;
      data[index]['#'] = `LOG${value?.sys_id}`
      const timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
      let dt = new Date(value?.created_at);
      let date = dt.getDate();
      let month = dt.toLocaleString('default', { month: 'short' });
      let year = dt.getFullYear();
      let time = dt.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true })
      data[index]['created_at'] = month + " " + date + " " + year + " | " + time;
      data[index]['description'] = (value?.description) ? (value?.description) : '';
      data[index]['title'] = (value?.title) ? (value?.title) : '';
      data[index]['file_request'] = (value?.file_request) ? (value?.file_request) : '';
      data[index]['credit_update_type'] = (value?.credit_update_type) ? (value?.credit_update_type) : '';
      data[index]['credit_update_description'] = (value?.credit_update_description) ? (value?.credit_update_description) : '';
    });

    let today = new Date().toLocaleDateString("de-DE");
    let filename = "log-report-" + today.replaceAll(".", "-");
    this._logreportservice.downloadInvoiceFile(data, filename);
  }
}
